@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap);
:root {
  --primary: #f7f0f0;
  --secondary: #18a999;
  --box: #d5896f;
  --font-colour: #484349;
  --dark: #484349;
  --dark-font: #eee;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}

body {
  background-color: #f7f0f0;
  background-color: var(--primary);
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

/* .App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  /* text-align: center; */
/* } */ 

.calculator {
  width: 100px;
  min-width: 400px;
  background: #d5896f;
  background: var(--box);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 16px;
  text-align: right;
  font-size: 30px;
  background-color: #484349;
  background-color: var(--dark);
  color: #eee;
  color: var(--dark-font);
  font-weight: 500;
}

button {
  border: none;
  /* border-radius: 0.5rem; */
  -webkit-appearance: none;
          appearance: none;
  /* width: 4em; */
  height: 3.5em;
  background-color: #18a999;
  background-color: var(--secondary);
  color: #eee;
  color: var(--dark-font);
  font-size: 18px;
  font-weight: 700;
  /* margin: 1px 0px 0px 1px; */
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
}

button:hover {
    opacity: 0.9;
}

.operators {
    display: flex;
}

.operators button {
    flex: 1 1;
    background-color: #f7f0f0;
    background-color: var(--primary);
    color: #484349;
    color: var(--font-colour);
    
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: #18a999;
  background-color: var(--secondary);
}

